import { Card, Progress } from "shadcn"; // Import ShadCN components

export default async function Page() {
  return (
    <div className="space-y-8">
      <h1 className="text-4xl font-bold">Challenges TODO</h1>

      {/* Level Progress Card */}
      <Card className="shadow-lg p-6">
        <div className="flex justify-between items-center">
          <h2 className="text-xl font-semibold">Current Level: Platinum Lifter</h2>
          <span className="text-sm text-gray-500">Experience required till Diamond Lifter</span>
        </div>
        <Progress value={33.33} max={100} className="my-4" />
        <div className="flex justify-between">
          <span className="text-sm text-gray-500">0%</span>
          <span className="text-sm text-gray-500">33.33%</span>
        </div>
      </Card>

      {/* Challenge Cards */}
      <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
        {/* Challenge 1 */}
        <Card className="shadow-lg p-4">
          <h3 className="font-semibold">Challenge 1: Sprint Training</h3>
          <p className="text-sm text-gray-500">Complete 5 sprints at 80% max effort.</p>
          <button className="mt-4 text-blue-500">Accept Challenge</button>
        </Card>

        {/* Challenge 2 */}
        <Card className="shadow-lg p-4">
          <h3 className="font-semibold">Challenge 2: Strength Building</h3>
          <p className="text-sm text-gray-500">Lift 10% more weight for 3 sets of deadlifts.</p>
          <button className="mt-4 text-blue-500">Accept Challenge</button>
        </Card>

        {/* Add more challenges here as needed */}
      </div>
    </div>
  );
}
